<h1 id="商户升级" tabindex="-1"><a class="header-anchor" href="#商户升级" aria-hidden="true">#</a> 商户升级</h1>
<h2 id="提交升级申请单" tabindex="-1"><a class="header-anchor" href="#提交升级申请单" aria-hidden="true">#</a> 提交升级申请单</h2>
<p>使用“提交升级申请单”接口为小微商户发起升级流程，根据商户实际情况可升级为个体户、企业、党政、机关及事业单位、其他组织。。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token function">upgrade</span><span class="token punctuation">(</span><span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'organization_type'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'2'</span><span class="token punctuation">,</span> <span class="token comment">// 主体类型</span>
    <span class="token string single-quoted-string">'business_license_copy'</span>  <span class="token operator">=></span> <span class="token string single-quoted-string">'media_id'</span><span class="token punctuation">,</span> <span class="token comment">// 营业执照扫描件</span>
    <span class="token comment">// ...</span>
    <span class="token comment">// 参数太多就不一一列出，自行根据 (小微商户专属文档 -> 提交升级申请单API) 填写</span>
<span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><h2 id="查询升级申请单状态" tabindex="-1"><a class="header-anchor" href="#查询升级申请单状态" aria-hidden="true">#</a> 查询升级申请单状态</h2>
<p>使用“提交升级申请单”接口后，可不定期调用此接口查询申请单状态（建议提交申请后1分钟查询），直至申请单为“完成”状态。</p>
<p>1)若申请状态为待账户验证，请按接口中的指引完成账户验证</p>
<p>2)若申请状态为审核中，微信支付会在2个工作日内完成资料审核</p>
<p>3)若申请状态为待签约，接口会返回签约二维码</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token function">getUpgradeStatus</span><span class="token punctuation">(</span><span class="token keyword type-hint">string</span> <span class="token variable">$subMchId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><blockquote>
<p>调用该接口前调用过 <code>setSubMchId</code> 方法则无需传入 <code>$subMchId</code> 参数</p>
